import React, { useState, useEffect } from 'react';
import { Helmet } from 'react-helmet-async';

import './Apart.css';
// import './BookingForm.css';

import YandexMap from '../YandexMap/YandexMap.jsx'
const Apart = ({ insertApart }) => {

  const {
    lot,
    name,
    amountRoom,
    place,
    floor,
    balcony,
    technics,
    communications,
    comfort,
    deposit,
    amountQuest,
    likeChildren,
    likeAnimals,
    likeSmoke,
    likeParty,
    accountingDocuments,
    adress,
    coordinatesX,
    coordinatesY,
    description,
    homeInfo,
    parking,
  } = insertApart;


  return (
    <section className='apart'>
      <h2>{name}</h2>
      <h3> </h3>
      <ul>
        <li> : <span>{amountRoom}</span></li>
        <li> : <span>{place}</span></li>
        <li>: <span>{floor}</span></li>
        <li>  : <span>{balcony}</span></li>
        <li>: <span>{technics}</span></li>
        <li>  : <span>{communications}</span></li>
        <li>: <span>{comfort}</span></li>
        <li>: <span>{deposit}</span></li>
      </ul>
      <h3></h3>
      <ul>
        <li> : <span>{amountQuest}</span></li>
        <li>  : <span>{likeChildren}</span></li>
        <li>  : <span>{likeAnimals}</span></li>
        <li> : <span>{likeSmoke}</span></li>
        <li> : <span>{likeParty}</span></li>
        <li>  : <span>{accountingDocuments}</span></li>
      </ul>
      <h3></h3>
      <p>{adress}</p>
      <YandexMap
        coordinatesX={coordinatesX}
        coordinatesY={coordinatesY}
      ></YandexMap>
      <h3></h3>
      <p className='apart__description'>{description}</p>
      <h3> </h3>
      <ul>
        <li>  : <span>{homeInfo}</span></li>
        <li>: <span>{parking}</span></li>
      </ul>

      <div
        id={`realtycalendar-widget-container-${lot}`}
        className='realty-calendar-widget-wrap'></div>
      <div
        className=''
        id="rc-small-bookings-widget-root"></div>

      <Helmet>
        <title>{`${name} | Good Vibes `}</title>
        {/* <!--    --> */}
        {/* <!--  --> */}
        <script
          async='true'
          src={`https://realtycalendar.ru/widgets/script?id=${lot}`}
          type='text/javascript'></script>
        {/* <!--   --> */}
        <script
          type="text/javascript">RC_SMALL_BOOKINGS_WIDGET.init('18a682d5b6e8dd87c30d92cbf743d596');
        </script>

      </Helmet>


    </section>

  );
}

export default Apart;
